import React, { Component } from 'react';
import FloatingLabelInput from "react-floating-label-paper-input";
import styled from 'styled-components';

const ContactWrapper = styled.div`
  width: 25vw;
  height: auto;
  position: absolute;
  right: 72px;
  top: 72px;
  background: #fff;
  box-shadow: 0 2px 16px -2px rgba(159,159,159,0.32);
`;

const ContactForm = styled.form`
  width: 100%;
  padding: 24px;
  box-sizing: border-box;
  display: flex;
  flex-flow: column nowrap;
`;

const ContactFormRow = styled.div`
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
`;

class Contact extends Component {

  render() {
    return (
      <ContactWrapper className="contact">
        <ContactForm className="contact__form" netlify method="POST">
          <ContactFormRow className="contact__form--row">
            <FloatingLabelInput
              type="text"
              name="firstname"
              labelName="First name"
            />
            <FloatingLabelInput
              type="text"
              name="lastname"
              labelName="Last name"
            />
          </ContactFormRow>
          <input type="submit" value="Submit" />
        </ContactForm>
      </ContactWrapper>
    );
  }

}

export default Contact;



